

















export default {
	data() {
		return {
			showClearIcon: false,
			inputClearValue: "",
			loading:false
		}
	},
	onLoad(e) {
		if(e.nickname){
			this.inputClearValue = e.nickname;
			this.showClearIcon = true;
		}
	},
	onShow() {
	},
	onShareAppMessage() {
		return {
			title: this.webshareTitle,
			path: '/pages/tabBar/index/index'
		}
	},
	methods: {
		bindClearInput: function (e) {
			this.inputClearValue = e.target.value;
			if (e.target.value.length > 0) {
				this.showClearIcon = true;
			} else {
				this.showClearIcon = false;
			}
		},
		clearIcon: function () {
			this.inputClearValue = "";
			this.showClearIcon = false;
		},
		setNickname: function () {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const nickname = this.inputClearValue;
			if(nickname==''){
				uni.showModal({
					content: '请填写昵称',
					showCancel: false
				});
				return;
			}
			const that = this;
			try {
				if(this.loading){
					return;
				}
				this.loading = true;
				
				this.$http.post(this.websiteUrl + '/api/api/setNickname', {
					openid,
					sessionKey,
					nickname
				}, (res) => {
					uni.showModal({
						content:res.data.msg,
						showCancel:false,
						success:function() {
							that.loading = false;
						}
					})
					
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false,
					success:function() {
						that.loading = false;
					}
				});
			}
		}
	}
}
